}

/* Existing styles */

.category.selected, .category:active {
    background-color: #8b6914; /* A different, noticeable color for selected categories */
}

/* Keep the rest of your CSS unchanged */

.card {
    width: 280px;
    margin: 0 8px 16px;  /* Adjust horizontal and vertical spacing */
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    overflow: hidden;
    background: #ffffff;
    text-align: center;
    position: relative;
}
  .new {
    font-family: 'Times New Roman';
    
  }
  .d-flex {
    display: flex;
    flex-wrap: wrap;
    justify-content: left; /* Aligns items horizontally and centers them */
}
  .quicklook {
    display: none;
    position: absolute;
    bottom: 235px;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* semi-transparent overlay */
    color: white;
    padding: 10px 0;
    text-align: center;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease; /* Smooth transition for hover effect */
  }

  .card:hover .quicklook {
    display: block; /* Show the quicklook div when hovering over the card */
  }
  
  .card img {
    width: 100%; /* Image takes full width of the card */
    display: block; /* Removes bottom space under the image */
  }
  
  .card h3 {
    font-size: 18px; /* Smaller font size */
    margin: 12px 0 4px; /* Adjust spacing */
    color: #222222; /* Dark text color */
  }
  
  .card p {
    font-size: 16px; /* Adjusted font size for description */
    color: #666666; /* Lighter text color for the description */
    margin: 0 0 16px; /* Adjust spacing */
  }
.icons {
    font-size: 32px;
    height: 32px;
    width: 32px;
    color: #ddd;
    cursor: pointer;
}

.icons:hover {
    color: red;
}

.icon-con {
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 50%;
    padding: 7px;
    background-color: #fff;
    margin: 7px;
}

.links {
    text-decoration: none;
    font-size: 24px;
    color: #002f34;
    font-weight: bold;
}

.price-text {
    color: #002f34;

}

.clear-btn {
    height: 24px;
    background-color: #002f34;
    color: #fff;
    border-radius: 4px;
    font-size: 12px;
    outline: none;
    border: none;
    padding: 0px 15px;
    margin-top: 10px;
    margin-left: 10px;
}
.product-detail-page {
    font-family: 'Roboto', sans-serif;
    color: #333;
    background-color: #f5f5f5;
    line-height: 1.6;
    padding: 20px;
}



.product-detail-container {
    max-width: 1200px;
    margin: 0 auto;
    background: #fff;
    padding: 20px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
}

.image-wrapper {
    display: flex;
    flex-direction: row; /* Arrange items horizontally */
    align-items: center; /* Center items vertically */
    margin-right: 10px;
    
    /* Adjust margin between images */
  }
  
.product-title {
    text-align: center;
    color: #333;
  
    font-size: 2rem;
    border-bottom: 1px solid #eee;
    padding-bottom: 10px;
    font-family: 'Times New Roman', Times, serif;
}

.product-content {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-gap: 20px;
    gap: 20px;
    align-items: start;
    margin-top: 20px;
}

.product-images {
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.arrow {
    font-size: 24px; /* Adjust arrow size */
    cursor: pointer;
    padding: 15px
  }
.product-images img {
    width: auto;
    max-width: 100%;
    height: auto;
    border-radius: 4px;
    margin-bottom: 10px;

}

.product-info {
    display: flex;
    flex-direction: column;
}
.product-info .icon-text {
    margin-left: 10px; /* Adjust the space between the icon and the text */
  }

.product-price {
    font-size: 2rem;
    color: #007bff;
    font-weight: bold;
}

.product-name {
    font-size: 1.5rem;
    margin: 10px 0;
    color: #555;
    font-family: 'Times New Roman';
}

.product-description {
    font-size: 1rem;
    margin-bottom: 20px;
    color: #666;
    border-bottom: 1px solid #eee;
    padding-bottom: 10px;
}

.contact-button {
    background-color: black;
    color: white;
    max-width: 50%;
    padding: 10px 15px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.2s;
}

.contact-button2 {
    margin-top: 10px;
    background-color: black;
    color: white;
    max-width: 50%;
    padding: 10px 15px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.2s;
}

.contact-button:hover {
    background-color: white;
    color: black;
    border-color: black;
    border-radius: 3px
}
.contact-button2:hover {
    background-color: white;
    color: black;
    border-color: black;
    border-radius: 3px
}

.user-info {
    margin-top: 20px;
}

.username {
    font-size: 1.4rem;
    color: #17a2b8;
}

.user-contact, .user-email {
    font-size: 1.1rem;
    color: #666;
}

@media (max-width: 768px) {
    .product-content {
        flex-direction: column;
    }

    .product-images img {
        max-width: 100%;
    }
}

.option-buttons {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 10px;
    gap: 10px;
    margin-top: 20px;
}

.option-button {
    padding: 10px 20px;
    border: 1px solid #ddd;
    background-color: #f8f8f8;
    cursor: pointer;
    transition: all 0.2s;
}

.option-button:hover {
    background-color: #e9e9e9;
    border-color: #ccc;
}

/* Assuming 'Add to Basket' is a button on your page */
.add-to-basket {
    background-color: #c00;
    color: white;
    padding: 15px 30px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1.2rem;
    transition: background-color 0.2s;
}

.add-to-basket:hover {
    background-color: #a00;
}
.report-form-container {
    max-width: 500px;
    margin: 0 auto;
    padding: 20px;
    margin-top: 40px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f9f9f9;
}

.report-form-title {
    text-align: center;
    margin-bottom: 20px;
    font-size: 24px;
}

.report-form-textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    resize: vertical;
}

.report-form-button {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: black;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}

.report-form-button:hover {
    background-color: #0056b3;
}

